.remove-device {
  display: flex;
  padding: 40px 24px;
  flex-direction: column;
  align-items: center;

  & h3 {
    padding: 0;
  }

  .description {
    width: 352px;
    margin-top: 24px;
    margin-bottom: 40px;
  }

  .device-detail__edit__actions {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;
  }

  & button {
    margin-top: 0;
  }
}
